// Odebere waiting roli Discord u≈æivateli
export async function removeWaitingRole(discordId: string): Promise<boolean> {
  try {
    console.log(`Odeb√≠r√°m waiting roli u≈æivateli ${discordId}`);
    console.log(`Waiting Role ID: ${DISCORD_WAITING_ROLE_ID}`);
    
    const response = await fetch(
      `https://discord.com/api/v10/guilds/${DISCORD_GUILD_ID}/members/${discordId}/roles/${DISCORD_WAITING_ROLE_ID}`,
      {
        method: "DELETE",
        headers: {
          "Authorization": `Bot ${DISCORD_BOT_TOKEN}`,
          "X-Audit-Log-Reason": "Automatick√© odebr√°n√≠ waiting role po schv√°len√≠ whitelist ≈æ√°dosti"
        }
      }
    );

    if (response.ok || response.status === 204) {
      console.log(`‚úÖ Waiting role √∫spƒõ≈°nƒõ odebr√°na u≈æivateli ${discordId}`);
      return true;
    } else {
      const errorText = await response.text();
      console.error(`‚ùå Chyba p≈ôi odeb√≠r√°n√≠ waiting role (${response.status}):`, errorText);
      
      if (response.status === 403) {
        console.error("Bot nem√° opr√°vnƒõn√≠ 'Manage Roles'");
      } else if (response.status === 404) {
        console.error("U≈æivatel nem√° waiting roli nebo role neexistuje");
      }
      
      return false;
    }
  } catch (error) {
    console.error("‚ùå Kritick√° chyba p≈ôi odeb√≠r√°n√≠ waiting role:", error);
    return false;
  }
}

// P≈ôid√° waiting roli Discord u≈æivateli
export async function addWaitingRole(discordId: string): Promise<boolean> {
  try {
    console.log(`P≈ôid√°v√°m waiting roli u≈æivateli ${discordId}`);
    console.log(`Waiting Role ID: ${DISCORD_WAITING_ROLE_ID}`);

    // Ovƒõ≈ôit, ≈æe je ƒçlenem guildy
    const memberResponse = await fetch(
      `https://discord.com/api/v10/guilds/${DISCORD_GUILD_ID}/members/${discordId}`,
      {
        headers: {
          "Authorization": `Bot ${DISCORD_BOT_TOKEN}`,
        }
      }
    );

    if (!memberResponse.ok) {
      const memberError = await memberResponse.text();
      console.error(`U≈æivatel ${discordId} nebyl nalezen na serveru (${memberResponse.status}):`, memberError);
      return false;
    }

    const response = await fetch(
      `https://discord.com/api/v10/guilds/${DISCORD_GUILD_ID}/members/${discordId}/roles/${DISCORD_WAITING_ROLE_ID}`,
      {
        method: "PUT",
        headers: {
          "Authorization": `Bot ${DISCORD_BOT_TOKEN}`,
          "X-Audit-Log-Reason": "Automatick√© p≈ôid√°n√≠ waiting role p≈ôi zmƒõnƒõ statusu na pending",
          "Content-Length": "0"
        }
      }
    );

    if (response.ok || response.status === 204) {
      console.log(`‚úÖ Waiting role √∫spƒõ≈°nƒõ p≈ôid√°na u≈æivateli ${discordId}`);
      return true;
    } else {
      const errorText = await response.text();
      console.error(`‚ùå Chyba p≈ôi p≈ôid√°v√°n√≠ waiting role (${response.status}):`, errorText);
      if (response.status === 403) {
        console.error("Bot nem√° opr√°vnƒõn√≠ 'Manage Roles' nebo nem√° dostateƒçnƒõ vysokou roli");
      } else if (response.status === 404) {
        console.error("U≈æivatel nebo role nebyla nalezena");
      }
      return false;
    }
  } catch (error) {
    console.error("‚ùå Kritick√° chyba p≈ôi p≈ôid√°v√°n√≠ waiting role:", error);
    return false;
  }
}

// P≈ôid√° whitelist roli Discord u≈æivateli
export async function addWhitelistRole(discordId: string): Promise<boolean> {
  try {
    console.log(`P≈ôid√°v√°m whitelist roli u≈æivateli ${discordId}`);
    console.log(`Guild ID: ${DISCORD_GUILD_ID}`);
    console.log(`Role ID: ${DISCORD_WHITELIST_ROLE_ID}`);
    
    // Nejd≈ô√≠v zkontrolujeme, jestli u≈æivatel existuje na serveru
    const memberResponse = await fetch(
      `https://discord.com/api/v10/guilds/${DISCORD_GUILD_ID}/members/${discordId}`,
      {
        headers: {
          "Authorization": `Bot ${DISCORD_BOT_TOKEN}`,
        }
      }
    );

    if (!memberResponse.ok) {
      const memberError = await memberResponse.text();
      console.error(`U≈æivatel ${discordId} nebyl nalezen na serveru (${memberResponse.status}):`, memberError);
      return false;
    }

    // P≈ôid√°me roli
    const response = await fetch(
      `https://discord.com/api/v10/guilds/${DISCORD_GUILD_ID}/members/${discordId}/roles/${DISCORD_WHITELIST_ROLE_ID}`,
      {
        method: "PUT",
        headers: {
          "Authorization": `Bot ${DISCORD_BOT_TOKEN}`,
          "X-Audit-Log-Reason": "Automatick√© p≈ôid√°n√≠ whitelist role po schv√°len√≠ ≈æ√°dosti"
        }
      }
    );

    if (response.ok || response.status === 204) {
      console.log(`‚úÖ Whitelist role √∫spƒõ≈°nƒõ p≈ôid√°na u≈æivateli ${discordId}`);
      
      // Po √∫spƒõ≈°n√©m p≈ôid√°n√≠ whitelist role odebereme waiting roli
      const waitingRemoved = await removeWaitingRole(discordId);
      console.log(`Waiting role odebr√°na: ${waitingRemoved}`);
      
      return true;
    } else {
      const errorText = await response.text();
      console.error(`‚ùå Chyba p≈ôi p≈ôid√°v√°n√≠ whitelist role (${response.status}):`, errorText);
      
      // Pokus√≠me se z√≠skat v√≠ce informac√≠ o chybƒõ
      if (response.status === 403) {
        console.error("Bot nem√° opr√°vnƒõn√≠ 'Manage Roles' nebo m√° ni≈æ≈°√≠ roli ne≈æ ta, kterou se sna≈æ√≠ p≈ôidat");
      } else if (response.status === 404) {
        console.error("U≈æivatel nebo role nebyla nalezena");
      }
      
      return false;
    }
  } catch (error) {
    console.error("‚ùå Kritick√° chyba p≈ôi p≈ôid√°v√°n√≠ whitelist role:", error);
    return false;
  }
}

